@media screen and (max-width: 639px) {
    .responsive-dropdown-menu {
        left: 0 !important;
        right: 0 !important;
        width: 100%;
        border-bottom: 1px solid rgba(0, 0, 0, 0.15);
        z-index: 1050
    }

    .responsive-dropdown-menu .dropdown-menu {
        -moz-transition: transform .3s ease-in;
        -o-transition: transform .3s ease-in;
        -webkit-transition: transform .3s ease-in;
        transition: transform .3s ease-in;
        top: 0 !important;
        left: 0 !important;
        right: 0 !important;
        width: 100% !important;
        height: 100%;
        border: 0;
        box-shadow: none
    }

    .responsive-dropdown-menu .dropdown-menu.active {
        -moz-transform: none;
        -o-transform: none;
        -ms-transform: none;
        -webkit-transform: none;
        transform: none
    }

    .responsive-dropdown-menu .dropdown-menu.current {
        -moz-transform: none;
        -o-transform: none;
        -ms-transform: none;
        -webkit-transform: none;
        transform: none
    }

    .responsive-dropdown-menu .dropdown-menu.conceal {
        -moz-transform: translateX(100%);
        -o-transform: translateX(100%);
        -ms-transform: translateX(100%);
        -webkit-transform: translateX(100%);
        transform: translateX(100%)
    }

    .responsive-dropdown-menu .dropdown-menu .item-back {
        display: table
    }
}

